'use client';
import React, { useState, useMemo } from 'react';

export default function OrderList({ orders: initialOrders }) {
  const [orders, setOrders] = useState(initialOrders);
  const [expandedOrder, setExpandedOrder] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  // ‚úÖ Memoize grouped orders to avoid recalculation on every render
  const groupedOrders = useMemo(() => {
    return Object.values(
      orders.reduce((acc, order) => {
        acc[order.order_number] ??= { ...order, related: [order] };
        acc[order.order_number].related.push(order);
        return acc;
      }, {})
    );
  }, [orders]);

  const toggleExpand = (orderNumber) => {
    setExpandedOrder((prev) => (prev === orderNumber ? null : orderNumber));
  };

  // ‚úÖ Reusable fetch wrapper
  const fetchOrders = async () => {
    try {
      const res = await fetch('http://oms-backend.test/api/orders');
      if (!res.ok) throw new Error('Failed to reload orders');
      const data = await res.json();
      setOrders(data);
    } catch (err) {
      console.error(err);
      alert('Error reloading orders');
    }
  };

  const handleCancelOrder = async () => {
    if (!selectedOrder) return;

    const payload = {
      total_amount: selectedOrder.total_amount,
      status: 'cancelled',
      order_number: selectedOrder.order_number,
      items: selectedOrder.order_item.map(({ product_id, unit_price, quantity }) => ({
        product_id,
        unit_price,
        quantity,
      })),
    };

    try {
      setIsLoading(true);
      const res = await fetch('http://oms-backend.test/api/orders', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      });

      if (!res.ok) throw new Error('Failed to cancel order');

      await fetchOrders();
      setShowModal(false);
      setSelectedOrder(null);
    } catch (err) {
      alert(`Error: ${err.message}`);
    } finally {
      setIsLoading(false);
    }
  };

  if (!orders?.length)
    return <p className="text-gray-500">No orders found.</p>;

  return (
    <div className="border rounded-lg shadow-sm overflow-hidden">
      <table className="min-w-full text-sm text-left">
        <thead className="bg-gray-100 border-b">
          <tr>
            {['#', 'Order Number', 'Status', 'Total', 'Created At'].map((header) => (
              <th key={header} className="px-4 py-2 font-medium text-gray-700">
                {header}
              </th>
            ))}
          </tr>
        </thead>

        <tbody>
          {groupedOrders.map((order, i) => (
            <React.Fragment key={order.order_number}>
              {/* Summary Row */}
              <tr
                onClick={() => toggleExpand(order.order_number)}
                className="border-b hover:bg-gray-50 transition-colors cursor-pointer"
              >
                <td className="px-4 py-2">{i + 1}</td>
                <td className="px-4 py-2 font-medium text-blue-600 underline">
                  {order.order_number}
                </td>
                <td className="px-4 py-2">
                  <span
                    className={`px-2 py-1 rounded text-xs font-medium ${
                      {
                        confirmed: 'bg-green-100 text-green-700',
                        cancelled: 'bg-red-100 text-red-700',
                      }[order.status] || 'bg-yellow-100 text-yellow-700'
                    }`}
                  >
                    {order.status}
                  </span>
                </td>
                <td className="px-4 py-2">
                  ‚Ç±{Number(order.total_amount).toFixed(2)}
                </td>
                <td className="px-4 py-2">
                  {new Date(order.created_at).toLocaleString()}
                </td>
              </tr>

              {/* Expanded Details */}
              {expandedOrder === order.order_number && (
                <tr className="bg-gray-50 border-b">
                  <td colSpan="5" className="px-6 py-4">
                    <OrderDetails order={order} onCancel={() => {
                      setSelectedOrder(order);
                      setShowModal(true);
                    }} />
                  </td>
                </tr>
              )}
            </React.Fragment>
          ))}
        </tbody>
      </table>

      {/* Cancel Confirmation Modal */}
      {showModal && selectedOrder && (
        <CancelModal
          order={selectedOrder}
          onCancel={handleCancelOrder}
          onClose={() => setShowModal(false)}
          isLoading={isLoading}
        />
      )}
    </div>
  );
}

/* ------------------ CHILD COMPONENTS ------------------ */

function OrderDetails({ order, onCancel }) {
  const hasItems = order.order_item?.length > 0;
  const hasStatuses = order.statuses?.length > 0;

  return (
    <>
      <h4 className="font-semibold text-lg mb-2">
        Order Details ({order.order_item?.length || 0} items)
      </h4>

      {/* Order Items */}
      {hasItems ? (
        <div className="space-y-2">
          {order.order_item.map((item) => (
            <div key={item.id} className="flex justify-between border-b pb-2">
              <div>
                <p className="font-medium">{item.product?.name || 'Unknown Product'}</p>
                <p className="text-sm text-gray-600">{item.product?.description}</p>
              </div>
              <p className="text-right">
                ‚Ç±{Number(item.unit_price).toFixed(2)} √ó {item.quantity}
              </p>
            </div>
          ))}
        </div>
      ) : (
        <p className="text-gray-500 text-sm italic">No items found for this order.</p>
      )}

      {/* Status Timeline */}
      {hasStatuses && (
        <div className="mt-4">
          <h5 className="font-semibold mb-1">Status Timeline</h5>
          <ul className="border rounded-md divide-y bg-white">
            {order.statuses.map((s, idx) => (
              <li
                key={idx}
                className={`p-2 flex justify-between text-sm ${
                  s.status === order.status
                    ? 'bg-green-50 font-medium text-green-700'
                    : 'text-gray-600'
                }`}
              >
                <span>{s.status}</span>
                <span>{new Date(s.created_at).toLocaleString()}</span>
              </li>
            ))}
          </ul>
        </div>
      )}

      {/* Cancel Button */}
      {order.status !== 'cancelled' && (
        <div className="mt-4">
          <button
            onClick={(e) => {
              e.stopPropagation();
              onCancel();
            }}
            className="bg-red-500 text-white px-4 py-2 rounded hover:bg-red-600"
          >
            Cancel Order
          </button>
        </div>
      )}
    </>
  );
}

function CancelModal({ order, onCancel, onClose, isLoading }) {
  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg shadow-lg w-full max-w-md p-6">
        <h2 className="text-lg font-semibold mb-4">Cancel Order</h2>
        <p className="text-gray-700 mb-6">
          Are you sure you want to cancel order{' '}
          <span className="font-bold">{order.order_number}</span>?
        </p>

        <div className="flex justify-end gap-3">
          <button
            onClick={onClose}
            className="px-4 py-2 bg-gray-300 rounded hover:bg-gray-400"
            disabled={isLoading}
          >
            No, Keep It
          </button>

          <button
            onClick={onCancel}
            className={`px-4 py-2 rounded text-white ${
              isLoading
                ? 'bg-red-300 cursor-not-allowed'
                : 'bg-red-500 hover:bg-red-600'
            }`}
            disabled={isLoading}
          >
            {isLoading ? 'Cancelling...' : 'Yes, Cancel Order'}
          </button>
        </div>
      </div>
    </div>
  );
}
